import React from 'react';

import AuthUserContext from './context';
import { withFirebase } from '../firebase';

import { connect } from 'react-redux'


const WAuthentication = Component => {
  class WithAuthentication extends React.Component {
    constructor(props) {
      super(props);

      this.state = {
        authUser: null,
      };
    }

    componentDidMount() {
      this.listener = this.props.firebase.auth.onAuthStateChanged(
        authUser => {

          this.props.firebase.users().doc(authUser.uid).get()
            .then((doc) => {
              if (doc.exists) {
                let user = doc.data()
                this.props.dispatch({ type: "addLevel", value: user.level })
                console.log('level user ' + this.props.level)
              }else{
                this.props.dispatch({ type: "addLevel", value: 'basic' })
                console.log('level user ' + this.props.level)
              }
            }).catch(function (error) {
              console.debug("Error  ", error);
            });
          authUser
            ? this.setState({ authUser })
            : this.setState({ authUser: null });
        },
      );
    }

    componentWillUnmount() {
      this.listener();
    }

    render() {
      return (
        <AuthUserContext.Provider value={this.state.authUser}>
          <Component {...this.props} />
        </AuthUserContext.Provider>
      );
    }
  }

  const mapStateToProps = (state) => {
    return {
      level: state.level
    }
  }



  return withFirebase((connect(mapStateToProps)(WithAuthentication)))

};

export default WAuthentication;